import React,{memo} from 'react'
import styled from 'styled-components';

import { useSelector,useDispatch } from 'react-redux';
import useMountRef from '../hook/useMounterRef'
import Spinner from '../components/Spinner';
import Table from '../components/Table';
import ErrorView from '../components/ErrorView';
import { getMovieRank } from '../slices/MovieRankSlice';
import BarChartView from '../components/BarChartView';

import dayjs from 'dayjs';

//    flex-box
const Container = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;

  .flex-item{
      flex-basis: 50%;
      box-sizing: border-box;
      padding: 10px;
  }
`;
const MovieRank = memo (() => {
    const dispatch = useDispatch();
    // Redux Store  Ajax  
    const {data, loading, error} = useSelector((state)=> state.movieRank)
    //       
    const [targetDt, setTargetDt] = React.useState(dayjs().add(-1,'d').format('YYYY-MM-DD'));
    
    //        hook
        const mountRef = useMountRef();
        //   
        const [chartData, setChartData]= React.useState();

    //           --> Ajax 
    React.useEffect(()=>{
        dispatch(getMovieRank({targetDt: targetDt.replaceAll("-","")}));
    },[dispatch,targetDt]);
    
    //     
    const onDateChange = React.useCallback((e)=>{
        e.preventDefault();
        //   . --> React.useEffect()    .
        setTargetDt(e.target.value);
    },[]);
    
    //Ajax       chartData  .
    //Ajax      ,        .
    React.useEffect(()=>{
        //       .
        if (mountRef.current){
            const newDate = {
                movieNm: [],
                audiCnt:[]  
            };
            data.boxOfficeResult.dailyBoxOfficeList.forEach((v,i)=>{
                newDate.movieNm.push(v.movieNm);
                newDate.audiCnt.push(v.audiCnt);
            });
            setChartData(newDate);
        }
    },[mountRef,data]);

  return (
    <div>
        <Spinner visible={loading}/> 

        <form>
            <input type='data' className='form-control' placeholder='' value={targetDt} onChange={onDateChange}/> 
        </form>

        <hr/>

        {error ? <ErrorView error={error}/> : (
            <Container>
            <div className='flex-item'>
                <BarChartView chartData={chartData}/>
            </div>
                <div className='flex-item'>
            <Table>
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th> </th>
                        <th></th>
                        <th>  </th>
                        <th> </th>
                    </tr>
                </thead>
                <tbody>
                    {data && data.boxOfficeResult.dailyBoxOfficeList.map((v,i)=>{
                        
                        return(
                            <tr key={i}>
                                <td>{v.rank}</td>
                                <td>{v.movieNm}</td>
                                <td>{Number(v.audiCnt).toLocaleString()}</td>
                                <td>{Number(v.salesAmt).toLocaleString()}</td>
                                <td>{Number(v.audiAcc).toLocaleString()}</td>
                                <td>{Number(v.salesAcc).toLocaleString()}</td>
                            </tr>
                        )
                    })}
                </tbody>
            </Table>
            </div>
        </Container>
        )}   
    </div>
  );
});

export default MovieRank;