import React,{memo} from 'react'
import{
    Chart as ChartJs,
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend,
} from 'chart.js';

import { Bar } from 'react-chartjs-2';

ChartJs.register (
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend
);

const BarChartView =memo (({chartDate})=> {
    /**   */

    const options={
        indexAxis:'x',
        responsive: true,
        plugins:{
            legend:{
                position:'bottom',
            },
            title:{
                display: true,
                text:'  ',
            }
        },
    };

    /** chart   () */
    const data ={
        //x  
        label:chartDate.movieNm,
        // y    
        datasets:[{
            // 
            label: ' ',
            backgroundColor: '#0066ff44',
            borderColor: '#0066ff',
            borderWidth: 1,
            //   y 
            data: chartDate.audiCnt,
        }]
    };
  return (
    
    <Bar 
    data={data}
    options={options}
    />
  )
});

BarChartView.defaultProps ={
    chartData: {
        movieNm: [], audiCnt:[]
    }
}

export default BarChartView;